  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

caption, th, td {
  text-align: center;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
